"use client";

import { useState, useEffect } from "react";
import Link from "next/link";
import dynamic from "next/dynamic";
import {
  MdStar,
  MdAccessTime,
  MdVerified,
  MdArrowBack,
  MdShoppingCart,
  MdCalendarToday,
  MdPerson,
  MdEditSquare,
} from "react-icons/md";
import { Card, CardContent } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Avatar, AvatarFallback, AvatarImage } from "@/components/ui/avatar";
import { Separator } from "@/components/ui/separator";
import { cn } from "@/lib/utils";
import { BuyHoursModal } from "./BuyHoursModal";

// Dynamically import InAppBuyHoursModal only when needed (avoids @whop/react import in standalone mode)
const InAppBuyHoursModal = dynamic(
  () => import("./InAppBuyHoursModal").then((mod) => mod.InAppBuyHoursModal),
  { ssr: false }
);

interface SellerProfileViewProps {
  experienceId: string;
  companyId?: string;
  seller: {
    id: string;
    name: string | null;
    avatar: string | null;
    sellerProfile: {
      id: string;
      hourlyRate: number;
      bio: string | null;
      tagline: string | null;
      averageRating: number | null;
      totalSessionsCompleted: number;
      totalReviews: number;
      isVerified: boolean;
    };
  };
  reviews: Array<{
    id: string;
    rating: number;
    text: string | null;
    createdAt: string;
    author: { id: string; name: string | null; avatar: string | null } | null;
  }>;
  existingBalance: number;
  isOwnProfile: boolean;
  showBuyModal?: boolean;
  isStandalone?: boolean;
  isAuthenticated?: boolean;
}

export function SellerProfileView({
  experienceId,
  companyId,
  seller,
  reviews,
  existingBalance,
  isOwnProfile,
  showBuyModal: initialShowBuyModal = false,
  isStandalone = false,
  isAuthenticated = false,
}: SellerProfileViewProps) {
  const [isBuyModalOpen, setIsBuyModalOpen] = useState(false);
  const { sellerProfile } = seller;
  const basePath = isStandalone ? "" : `/experiences/${experienceId}`;
  const browseUrl = isStandalone ? "/browse" : `${basePath}?tab=browse`;

  // Open modal automatically if ?action=buy is in the URL
  useEffect(() => {
    if (initialShowBuyModal && !isOwnProfile) {
      setIsBuyModalOpen(true);
    }
  }, [initialShowBuyModal, isOwnProfile]);

  const formatCurrency = (cents: number) => {
    return new Intl.NumberFormat("en-US", {
      style: "currency",
      currency: "USD",
      minimumFractionDigits: 0,
    }).format(cents / 100);
  };

  const formatHours = (units: number) => {
    const hours = units / 2;
    return hours === 1 ? "1 hour" : `${hours} hours`;
  };

  return (
    <div className="p-4 sm:p-6 md:p-8 max-w-4xl mx-auto">
      {/* Back Button */}
      <Link
        href={browseUrl}
        className="inline-flex items-center gap-2 text-muted-foreground hover:text-foreground mb-6 transition-colors"
      >
        <MdArrowBack className="h-4 w-4" />
        <span className="text-sm">Back to Browse</span>
      </Link>

      {/* Profile Header */}
      <Card className="mb-6">
        <CardContent className="p-6">
          <div className="flex flex-col sm:flex-row gap-6">
            {/* Avatar */}
            <Avatar className="h-24 w-24 sm:h-32 sm:w-32 mx-auto sm:mx-0">
              <AvatarImage src={seller.avatar || undefined} />
              <AvatarFallback className="text-3xl">
                {seller.name?.[0]?.toUpperCase() || "?"}
              </AvatarFallback>
            </Avatar>

            {/* Info */}
            <div className="flex-1 text-center sm:text-left">
              <div className="flex items-center gap-2 justify-center sm:justify-start mb-1">
                <h1 className="text-2xl font-bold">{seller.name || "Anonymous"}</h1>
                {sellerProfile.isVerified && (
                  <MdVerified className="h-5 w-5 text-primary" />
                )}
              </div>

              {sellerProfile.tagline && (
                <p className="text-muted-foreground mb-3">{sellerProfile.tagline}</p>
              )}

              {/* Stats Row */}
              <div className="flex items-center gap-4 justify-center sm:justify-start text-sm">
                {sellerProfile.averageRating && (
                  <div className="flex items-center gap-1">
                    <MdStar className="h-4 w-4 text-warning" />
                    <span className="font-medium">{sellerProfile.averageRating.toFixed(1)}</span>
                    <span className="text-muted-foreground">
                      ({sellerProfile.totalReviews} reviews)
                    </span>
                  </div>
                )}
                <div className="flex items-center gap-1 text-muted-foreground">
                  <MdCalendarToday className="h-4 w-4" />
                  <span>{sellerProfile.totalSessionsCompleted} sessions</span>
                </div>
              </div>

              {/* Rate */}
              <div className="mt-4">
                <span className="text-3xl font-bold text-primary">
                  {formatCurrency(sellerProfile.hourlyRate)}
                </span>
                <span className="text-muted-foreground">/hour</span>
              </div>
            </div>

            {/* Actions */}
            <div className="flex flex-col gap-3 sm:min-w-[160px]">
              {!isOwnProfile && (
                <>
                  {isStandalone && !isAuthenticated ? (
                    <Button
                      size="lg"
                      className="gap-2"
                      variant="secondary"
                      disabled
                      title="Login to purchase hours"
                    >
                      <MdShoppingCart className="h-5 w-5" />
                      Buy Hours
                    </Button>
                  ) : (
                    <Button
                      size="lg"
                      className="gap-2"
                      onClick={() => setIsBuyModalOpen(true)}
                    >
                      <MdShoppingCart className="h-5 w-5" />
                      Buy Hours
                    </Button>
                  )}
                  {existingBalance > 0 && !isStandalone && (
                    <Button variant="outline" size="lg" className="gap-2">
                      <MdAccessTime className="h-5 w-5" />
                      Book Session
                    </Button>
                  )}
                </>
              )}
              {isOwnProfile && !isStandalone && (
                <Link href={`${basePath}?tab=settings&mode=seller`}>
                  <Button variant="outline" size="lg" className="w-full gap-2 hover:bg-muted hover:text-foreground">
                    <MdEditSquare className="h-5 w-5" />
                    Edit Profile
                  </Button>
                </Link>
              )}
            </div>
          </div>

          {/* Existing Balance */}
          {existingBalance > 0 && !isOwnProfile && !isStandalone && (
            <div className="mt-6 p-4 bg-primary/5 rounded-xl border border-primary/20">
              <div className="flex items-center justify-between">
                <div className="flex items-center gap-2">
                  <MdAccessTime className="h-5 w-5 text-primary" />
                  <span className="font-medium">Your Balance</span>
                </div>
                <span className="text-lg font-semibold text-primary">
                  {formatHours(existingBalance)}
                </span>
              </div>
              <p className="text-sm text-muted-foreground mt-1">
                Ready to book a session
              </p>
            </div>
          )}

          {/* Standalone Mode Notice - only show for unauthenticated users */}
          {isStandalone && !isOwnProfile && !isAuthenticated && (
            <div className="mt-6 p-4 bg-muted/50 rounded-xl border border-border">
              <p className="text-sm text-muted-foreground">
                <a href="/login" className="text-primary hover:underline font-medium">Login</a> to purchase hours from this seller.
              </p>
            </div>
          )}
        </CardContent>
      </Card>

      {/* Bio Section */}
      {sellerProfile.bio && (
        <Card className="mb-6">
          <CardContent className="p-6">
            <h2 className="text-lg font-semibold mb-3 flex items-center gap-2">
              <MdPerson className="h-5 w-5 text-muted-foreground" />
              About
            </h2>
            <p className="text-foreground whitespace-pre-wrap leading-relaxed">
              {sellerProfile.bio}
            </p>
          </CardContent>
        </Card>
      )}

      {/* Reviews Section */}
      <Card>
        <CardContent className="p-6">
          <div className="flex items-center justify-between mb-4">
            <h2 className="text-lg font-semibold flex items-center gap-2">
              <MdStar className="h-5 w-5 text-yellow-500" />
              Reviews
            </h2>
            {sellerProfile.averageRating && (
              <div className="flex items-center gap-2">
                <div className="flex">
                  {[1, 2, 3, 4, 5].map((star) => (
                    <MdStar
                      key={star}
                      className={cn(
                        "h-4 w-4",
                        star <= Math.round(sellerProfile.averageRating!)
                          ? "text-yellow-500"
                          : "text-muted"
                      )}
                    />
                  ))}
                </div>
                <span className="font-medium">{sellerProfile.averageRating.toFixed(1)}</span>
              </div>
            )}
          </div>

          {reviews.length === 0 ? (
            <p className="text-muted-foreground text-center py-8">
              No reviews yet. Be the first to leave a review!
            </p>
          ) : (
            <div className="space-y-4">
              {reviews.map((review, index) => (
                <div key={review.id}>
                  {index > 0 && <Separator className="my-4" />}
                  <div className="flex gap-3">
                    <Avatar className="h-10 w-10">
                      <AvatarImage src={review.author?.avatar || undefined} />
                      <AvatarFallback>
                        {review.author?.name?.[0]?.toUpperCase() || "?"}
                      </AvatarFallback>
                    </Avatar>
                    <div className="flex-1">
                      <div className="flex items-center justify-between mb-1">
                        <span className="font-medium text-sm">
                          {review.author?.name || "Anonymous"}
                        </span>
                        <span className="text-xs text-muted-foreground">
                          {new Date(review.createdAt).toLocaleDateString()}
                        </span>
                      </div>
                      <div className="flex mb-2">
                        {[1, 2, 3, 4, 5].map((star) => (
                          <MdStar
                            key={star}
                            className={cn(
                              "h-3.5 w-3.5",
                              star <= review.rating ? "text-yellow-500" : "text-muted"
                            )}
                          />
                        ))}
                      </div>
                      {review.text && (
                        <p className="text-sm text-foreground">{review.text}</p>
                      )}
                    </div>
                  </div>
                </div>
              ))}
            </div>
          )}
        </CardContent>
      </Card>

      {/* Buy Hours Modal - render when user can purchase */}
      {(!isStandalone || isAuthenticated) && (
        isStandalone ? (
          <BuyHoursModal
            open={isBuyModalOpen}
            onOpenChange={setIsBuyModalOpen}
            seller={{
              id: seller.id,
              name: seller.name,
              avatar: seller.avatar,
              hourlyRate: sellerProfile.hourlyRate,
            }}
            experienceId={experienceId}
            companyId={companyId}
            isStandalone={isStandalone}
          />
        ) : (
          <InAppBuyHoursModal
            open={isBuyModalOpen}
            onOpenChange={setIsBuyModalOpen}
            seller={{
              id: seller.id,
              name: seller.name,
              avatar: seller.avatar,
              hourlyRate: sellerProfile.hourlyRate,
            }}
            experienceId={experienceId}
            companyId={companyId}
          />
        )
      )}
    </div>
  );
}
